body {
  margin: 0px;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  height: 100vh;
}

.container {
  padding-top: 80px;
  margin: auto;
  display: flex;
  max-width: 1200px;
  height: 100%;
  justify-content: center;
}

.loader {
  width: 100%;
  padding-top: 40vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}
.loader_text {
  font-size: 30px;
}

.movies {
  margin: 0px;
  padding: 0px;
}
